import { useContext } from 'react'
import { useHistory } from 'react-router-dom'
import { AuthContext } from '../utils/authorization'

function ServerErrorPage(){
    const userContext = useContext(AuthContext)
    const routeHistory = useHistory()
    const clickAction = (e)=>{
        e.preventDefault()
        //userContext.srvError('')
        if(e.target.value === 'Homepage'){
            routeHistory.push('/')
        }
        if(e.target.value === 'Back'){
            routeHistory.push(userContext.backPageURL)
        }
    }

    return (
        <div>
            <h4>Something went wrong!</h4>
            <p>{userContext.srvErrMessage}</p>
            <input type='submit' value='Homepage' onClick={clickAction} />
            <input type='submit' value='Back' onClick={clickAction} />
        </div>
    )
}

export default ServerErrorPage