import { useState } from 'react'
import { useLocation, useHistory } from 'react-router-dom'
import { useContext } from 'react';

import { AuthContext } from './authorization';

export const useFormUsageActions = (initValues, callbackAtSending)=>{

    const [ values, setFormValues ] = useState(initValues)
    const onChange = (event) =>{
        setFormValues({
            ...values, 
            [event.target.name]: event.target.value
        })
    }
    const onSubmit = (event)=>{
        event.preventDefault();
        callbackAtSending();
    }
    const clearFields = ()=>{
        setFormValues(initValues)
    }
    return {
        values,
        onSubmit,
        onChange,
        clearFields
    }
}

export const useFormFeedbackActions = ()=>{

    //initial value for compatibliy is always the same
    const [ errors, setSomeError ] = useState({ errfields: [], errmsgs: []  })
    const [ message, setMessage ] = useState('')
    //datas are comming from client validation module!
    //or server result - in case of validation error
    const setValidationErrors = (err)=>{
        setSomeError({  errfields: err.field, errmsgs: err.message })
    }
    const setGeneralError = (errormessage)=>{
        setSomeError({  errfields: [], errmsgs: errormessage })
    }
    const resetErrors = ()=>{
        setSomeError({ errfields: [], errmsgs: [] })
    }
    const setGeneralMessage = (message)=>{
        setMessage( message )
    }
    const resetMessage = ()=>{
        setMessage('' )
    }
    const resetFeedbacks = ()=>{
        setSomeError({ errfields: [], errmsgs: [] })
        setMessage( '' )
    }
    return {
        errors, message,
        setValidationErrors,
        setGeneralMessage,
        setGeneralError,
        resetErrors,
        resetMessage,
        resetFeedbacks
    }
}


export const usePathGetter = ()=>{
    const actLocat = useLocation()
    return actLocat.pathname.split('/')
}

export const useLogRestriction = (logstatePreferation, destinAtRestrict)=>{
    const history = useHistory()    
    const userContext = useContext(AuthContext)
    if( (userContext.user && logstatePreferation === 'LOGOUT_NEEDED') ||
        (!userContext.user && logstatePreferation === 'LOGIN_NEEDED') ){
        history.push(destinAtRestrict)
    }
}

export const useCookieErrorReport = (destinAtError, progPrevDestin)=>{
    const userContext = useContext(AuthContext)
    const history = useHistory()
    const cookies = document.cookie
    if(cookies){
        const cookieErrorMsg = cookies.split(';')
            .find( row => row.startsWith('srverror='))
            .split('=')[1]
        if(cookieErrorMsg){
            const txtToRender = cookieErrorMsg.split('_').join(' ')
            userContext.setSrvError(txtToRender)
            if(progPrevDestin){
                userContext.setBackPageURL(progPrevDestin)
                history.replace(progPrevDestin)
            }
            history.push(destinAtError)
        }
    }
}
